import Section from "../../common/Section"
import Header from "../../common/Header"
import Main from "../../common/Main"
import Nav from "../../common/Nav";
import { Article, Content, Img, Title } from "./styled";

function Autor() {
    return (
        <>
            <Nav />
            <Main>
                <Header title="O autorze" />
                <Section
                    title="Nazar Bychyk"
                    body={
                        <Article>
                            <Content>
                                <Title>Witam wszystkich!</Title> <br />
                                Nazywam si Nazar, mam 22 lata.<br />
                                Urodziem si w miecie niedaleko Tarnopola lecym na terenach Ukrainy.<br />
                                W wieku 17 lat przyjechaem do Warszawy.<br />
                                Lubi jachty a cakiem niedawno polubiem programowa!
                            </Content>
                            <Img src={require("./author.jpeg")} alt={"Author"}></Img>
                        </Article>}>
                </Section>
            </Main>
        </>
    );
};

export default Autor;
